"use client";

import { useState, useEffect, useCallback, useRef } from "react";
import {
  ProviderMonitoringData,
  UsageMetrics,
  HealthStatus,
} from "@/components/ui/provider-monitoring-dashboard";
import {
  ProviderMonitoringService,
  AlertEvent,
  MonitoringConfig,
} from "@/lib/services/provider-monitoring-service";

export interface UseProviderMonitoringOptions {
  autoStart?: boolean;
  refreshInterval?: number;
  enableAlerts?: boolean;
  initialProviders?: ProviderMonitoringData[];
  config?: Partial<MonitoringConfig>;
}

export interface UseProviderMonitoringReturn {
  // Data
  providers: ProviderMonitoringData[];
  alerts: AlertEvent[];
  isMonitoring: boolean;
  loading: boolean;
  error: string | null;

  // Controls
  start: () => Promise<void>;
  stop: () => void;
  refresh: () => Promise<void>;

  // Provider management
  updateProvider: (
    id: string,
    updates: Partial<ProviderMonitoringData>,
  ) => void;
  recordRequest: (
    providerId: string,
    success: boolean,
    inputTokens: number,
    outputTokens: number,
    latency: number,
    cost: number,
    errorType?: string,
  ) => void;

  // Health checks
  performHealthCheck: (providerId?: string) => Promise<void>;

  // Alerts
  acknowledgeAlert: (alertId: string) => void;
  getActiveAlerts: () => AlertEvent[];

  // Statistics
  getStats: () => {
    totalProviders: number;
    healthyProviders: number;
    enabledProviders: number;
    totalRequests: number;
    totalCost: number;
    averageLatency: number;
    successRate: number;
    activeAlerts: number;
  };

  // Export
  exportData: (timeRange?: { start: Date; end: Date }) => any;
}

/**
 * Hook for real-time provider monitoring
 */
export function useProviderMonitoring(
  options: UseProviderMonitoringOptions = {},
): UseProviderMonitoringReturn {
  const {
    autoStart = true,
    refreshInterval = 30000,
    enableAlerts = true,
    initialProviders = [],
    config,
  } = options;

  const [providers, setProviders] =
    useState<ProviderMonitoringData[]>(initialProviders);
  const [alerts, setAlerts] = useState<AlertEvent[]>([]);
  const [isMonitoring, setIsMonitoring] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const monitoringService = useRef<ProviderMonitoringService>();
  const unsubscribeProviders = useRef<(() => void) | null>(null);
  const unsubscribeAlerts = useRef<(() => void) | null>(null);

  // Initialize monitoring service
  useEffect(() => {
    monitoringService.current = ProviderMonitoringService.getInstance();

    // Subscribe to provider updates
    unsubscribeProviders.current = monitoringService.current.subscribe(
      (data) => {
        setProviders(data);
      },
    );

    // Subscribe to alerts if enabled
    if (enableAlerts) {
      unsubscribeAlerts.current = monitoringService.current.subscribeToAlerts(
        (alert) => {
          setAlerts((prev) => [alert, ...prev].slice(0, 50)); // Keep last 50 alerts
        },
      );
    }

    return () => {
      unsubscribeProviders.current?.();
      unsubscribeAlerts.current?.();
    };
  }, [enableAlerts]);

  // Fetch initial provider data
  const fetchProviderData = useCallback(async (): Promise<
    ProviderMonitoringData[]
  > => {
    try {
      const response = await fetch("/api/llm/providers");
      if (!response.ok) {
        throw new Error(`Failed to fetch providers: ${response.statusText}`);
      }

      const data = await response.json();
      if (!data.success) {
        throw new Error(data.error || "Failed to fetch providers");
      }

      // Transform API data to monitoring format
      return data.providers.map((provider: any) => ({
        id: provider.id,
        name: provider.name,
        type: provider.type,
        enabled: provider.enabled,
        usage: {
          totalRequests: provider.usage.totalRequests || 0,
          successfulRequests: provider.usage.successfulRequests || 0,
          failedRequests: provider.usage.failedRequests || 0,
          totalInputTokens: provider.usage.totalInputTokens || 0,
          totalOutputTokens: provider.usage.totalOutputTokens || 0,
          totalCost: provider.usage.totalCost || 0,
          averageLatency: provider.usage.averageLatency || 0,
          requestsPerMinute: 0,
          costPerHour: 0,
          errorRate: 0,
          lastUpdated: new Date(),
        },
        health: {
          isHealthy: provider.status.isHealthy || false,
          status: provider.status.isHealthy ? "healthy" : "unhealthy",
          responseTime: provider.status.responseTimeMs || 0,
          uptime: 100,
          lastCheck: new Date(),
          errorCount: provider.status.errorCount || 0,
          consecutiveFailures: 0,
        },
        rateLimits: {
          requestsPerMinute: provider.configuration.maxRequestsPerMinute || 60,
          requestsRemaining: provider.configuration.maxRequestsPerMinute || 60,
          tokensPerMinute: 100000,
          tokensRemaining: 100000,
        },
        trends: {
          requestsHistory: [],
          latencyHistory: [],
          costHistory: [],
          errorHistory: [],
        },
      }));
    } catch (err) {
      console.error("[USE-MONITORING] Failed to fetch provider data:", err);
      throw err;
    }
  }, []);

  // Start monitoring
  const start = useCallback(async () => {
    if (isMonitoring) return;

    setLoading(true);
    setError(null);

    try {
      let providersToMonitor = providers;

      // If no providers in state, fetch from API
      if (providersToMonitor.length === 0) {
        providersToMonitor = await fetchProviderData();
        setProviders(providersToMonitor);
      }

      // Initialize monitoring service
      await monitoringService.current?.initialize(providersToMonitor, config);
      setIsMonitoring(true);

      console.log(
        "[USE-MONITORING] Started monitoring for",
        providersToMonitor.length,
        "providers",
      );
    } catch (err) {
      const errorMessage =
        err instanceof Error ? err.message : "Failed to start monitoring";
      setError(errorMessage);
      console.error("[USE-MONITORING] Failed to start:", err);
    } finally {
      setLoading(false);
    }
  }, [isMonitoring, providers, fetchProviderData, config]);

  // Auto-start monitoring
  useEffect(() => {
    if (autoStart && initialProviders.length > 0) {
      start();
    }
  }, [autoStart, initialProviders.length, start]);

  // Stop monitoring
  const stop = useCallback(() => {
    monitoringService.current?.stop();
    setIsMonitoring(false);
    console.log("[USE-MONITORING] Stopped monitoring");
  }, []);

  // Refresh data
  const refresh = useCallback(async () => {
    if (!isMonitoring) return;

    setLoading(true);
    try {
      const freshData = await fetchProviderData();
      setProviders(freshData);

      // Update monitoring service with fresh data
      for (const provider of freshData) {
        monitoringService.current?.updateProvider(
          provider.providerId,
          provider,
        );
      }
    } catch (err) {
      const errorMessage =
        err instanceof Error ? err.message : "Failed to refresh data";
      setError(errorMessage);
    } finally {
      setLoading(false);
    }
  }, [isMonitoring, fetchProviderData]);

  // Update provider
  const updateProvider = useCallback(
    (id: string, updates: Partial<ProviderMonitoringData>) => {
      monitoringService.current?.updateProvider(id, updates);
    },
    [],
  );

  // Record request
  const recordRequest = useCallback(
    (
      providerId: string,
      success: boolean,
      inputTokens: number,
      outputTokens: number,
      latency: number,
      cost: number,
      errorType?: string,
    ) => {
      monitoringService.current?.recordRequest(
        providerId,
        success,
        inputTokens,
        outputTokens,
        latency,
        cost,
        errorType,
      );
    },
    [],
  );

  // Perform health check
  const performHealthCheck = useCallback(
    async (providerId?: string) => {
      try {
        const payload = providerId
          ? { providerId }
          : { providerIds: providers.map((p) => p.providerId) };

        const response = await fetch("/api/llm/health-check", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(payload),
        });

        if (!response.ok) {
          throw new Error(`Health check failed: ${response.statusText}`);
        }

        const data = await response.json();

        if (data.success && data.results) {
          // Update provider health status
          Object.entries(data.results).forEach(
            ([id, result]: [string, any]) => {
              const healthUpdate: Partial<ProviderMonitoringData> = {
                health: {
                  status: result.isHealthy ? "healthy" : "unhealthy",
                  lastCheck: new Date(result.timestamp),
                  uptime: result.isHealthy ? 99.9 : 0,
                  incidents: [],
                },
              };
              monitoringService.current?.updateProvider(id, healthUpdate);
            },
          );
        }
      } catch (err) {
        console.error("[USE-MONITORING] Health check failed:", err);
        setError(err instanceof Error ? err.message : "Health check failed");
      }
    },
    [providers],
  );

  // Acknowledge alert
  const acknowledgeAlert = useCallback((alertId: string) => {
    monitoringService.current?.acknowledgeAlert(alertId);
    setAlerts((prev) =>
      prev.map((alert) =>
        alert.id === alertId ? { ...alert, acknowledged: true } : alert,
      ),
    );
  }, []);

  // Get active alerts
  const getActiveAlerts = useCallback((): AlertEvent[] => {
    return alerts.filter((alert) => !alert.acknowledged);
  }, [alerts]);

  // Get statistics
  const getStats = useCallback(() => {
    const totalProviders = providers.length;
    const healthyProviders = providers.filter(
      (p) => p.health.status === "healthy",
    ).length;
    const enabledProviders = providers.length; // All monitored providers are considered enabled
    const totalRequests = providers.reduce(
      (sum, p) => sum + p.metrics.requestCount,
      0,
    );
    const successfulRequests = providers.reduce(
      (sum, p) => sum + (p.metrics.requestCount * p.metrics.successRate) / 100,
      0,
    );
    const totalCost = providers.reduce(
      (sum, p) => sum + p.metrics.costEstimate,
      0,
    );
    const totalLatency = providers.reduce(
      (sum, p) => sum + p.metrics.averageLatency,
      0,
    );
    const averageLatency =
      totalProviders > 0 ? totalLatency / totalProviders : 0;
    const successRate =
      totalRequests > 0 ? (successfulRequests / totalRequests) * 100 : 0;
    const activeAlerts = getActiveAlerts().length;

    return {
      totalProviders,
      healthyProviders,
      enabledProviders,
      totalRequests,
      totalCost,
      averageLatency,
      successRate,
      activeAlerts,
    };
  }, [providers, getActiveAlerts]);

  // Export data
  const exportData = useCallback((timeRange?: { start: Date; end: Date }) => {
    return monitoringService.current?.exportData(timeRange);
  }, []);

  // Get alerts from monitoring service on load
  useEffect(() => {
    if (isMonitoring && enableAlerts) {
      const serviceAlerts = monitoringService.current?.getAlerts() || [];
      setAlerts(serviceAlerts);
    }
  }, [isMonitoring, enableAlerts]);

  return {
    providers,
    alerts,
    isMonitoring,
    loading,
    error,
    start,
    stop,
    refresh,
    updateProvider,
    recordRequest,
    performHealthCheck,
    acknowledgeAlert,
    getActiveAlerts,
    getStats,
    exportData,
  };
}

/**
 * Hook for monitoring a specific provider
 */
export function useProviderMonitoringById(providerId: string) {
  const monitoring = useProviderMonitoring();

  const provider = monitoring.providers.find(
    (p) => p.providerId === providerId,
  );
  const providerAlerts = monitoring.alerts.filter(
    (a) => a.providerId === providerId,
  );

  const updateProvider = useCallback(
    (updates: Partial<ProviderMonitoringData>) => {
      monitoring.updateProvider(providerId, updates);
    },
    [monitoring, providerId],
  );

  const performHealthCheck = useCallback(async () => {
    await monitoring.performHealthCheck(providerId);
  }, [monitoring, providerId]);

  const recordRequest = useCallback(
    (
      success: boolean,
      inputTokens: number,
      outputTokens: number,
      latency: number,
      cost: number,
      errorType?: string,
    ) => {
      monitoring.recordRequest(
        providerId,
        success,
        inputTokens,
        outputTokens,
        latency,
        cost,
        errorType,
      );
    },
    [monitoring, providerId],
  );

  return {
    provider,
    alerts: providerAlerts,
    isMonitoring: monitoring.isMonitoring,
    loading: monitoring.loading,
    error: monitoring.error,
    updateProvider,
    performHealthCheck,
    recordRequest,
  };
}
